import {ajaxErrorLog,dialogHandle} from '../dialog';
function siteNewAddDataDayHandle(options){
    return {
        type:"SITENEWADDDATADAY_HANDLE",
        options
    }
}

function siteNewAddDataDayLoadData(options){
	return dispatch=>{
		$.XlAjax({
            url: "nodehasdataincMonthlyReport",
            type:"GET",
            data: options.data,
            success: function(response){
            	if(response.status == "0000"){
            		let list = response.data.data;
            		if(list.length>0){
            			dispatch(
            				siteNewAddDataDayHandle({
		                        tableList:list,
		                        tbodyList:"success",
		                        totalSize:response.data.count
		                    })
            			);
            		}else{
            			dispatch(
            				siteNewAddDataDayHandle({
		                        tableList:list,
		                        tbodyList:"nothing",
		                        totalSize:response.data.count
		                    })
            			);
            		}            		
            	}else{
            		dispatch(
            			siteNewAddDataDayHandle({
	                        tbodyList:"fail"
	                    })
            		);
            		dispatch(
            			dialogHandle({
            				show:true,
	                        type:"tips",
	                        tipsType:'fail',
	                        width:'300px',
	                        height:'300px',
	                        content:response.message,
	                        title:'提示',
            			})
            		);
            	}
            	
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        });
	}
}

function siteNewAddDataDayExport(options){
    return dispatch=>{
        $.XlAjax({
            url:"nodehasdataincMonthlyExportExcel",
            type:"GET",
            data:options,
            success:function(data){
                if(data.status=="0000"){
                    window.location.href=data.data.url;
                }else{
                    dispatch(dialogHandle({
                        show:true,
                        type:'tips',
                        tipsType:'warning',
                        content:data.message
                    }))
                }
            },
            errorDialog:function(xhr, errorType, error){
                ajaxErrorLog(xhr, errorType, error,dispatch);
            }
        })
    }
}

export  {siteNewAddDataDayHandle,siteNewAddDataDayLoadData,siteNewAddDataDayExport};